*{
    box-sizing: border-box;
}
nav{
    width: 100%;
    /* background-color: antiquewhite; */
    height: 58px;
    display: flex;
}
.navbar-brand {
    height: 58px;
    margin-left: 30px;
    margin-top: 10px;
}
.navbar-nav{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    grid-gap: 15px;
    gap: 15px;
    float: left; 
    margin-top: 10px;
    padding-left: 15rem;
}
.navbar-brand label{
    font-weight: 700;
    font-size: 24px;
